import { React, useState } from 'react'
import Check from '../tools/Check'
import './Login.css'
import 'bootstrap/dist/css/bootstrap.min.css'
// import { InputGroup, Form } from 'react-bootstrap'

const Login = () => {
  const [log, setLog] = useState('');
  let on = false;
  const ync = async () => {
    if (await Check(localStorage.getItem('port'), localStorage.getItem('pwd'))) {
      window.location.replace(`/dasboard/0/home`)
    }else {
      if (on){
        setLog('Err Password or port wrong');
      };
    };
    on = true;
  };
  ync()
  
  return (
    <div>
      <div className="Auth-form-container">
      <div className="Auth-form">
        <div className="Auth-form-content">
          <h3 className="Auth-form-title">Sign In</h3>
          <div className="form-group mt-3">
            <label>Port</label>
            <input
              type="username"
              className="form-control mt-1"
              placeholder="Enter Server IP/Port"
              onChange={e => localStorage.setItem("port", e.target.value)}
            />
          </div>
          <div className="form-group mt-3">
            <label>Password</label>
            <input
              type="password"
              className="form-control mt-1"
              placeholder="Enter password"
              onChange={e => localStorage.setItem("pwd", e.target.value)}
            />
          </div>
          <div className="d-grid gap-2 mt-3">
            <button className="btn btn-primary" onClick={() => ync()}>
              Submit
            </button>
            <p className='text-danger'>{log}</p>
          </div>
        </div>
      </div>
    </div>
    </div>
  )
}

export default Login