import { Route, Routes } from 'react-router-dom';
import Login from './pages/Login';
import Dasboard from './pages/Dasboard';

function App() {
  return (
    <Routes>
      <Route exact path="/" element={<Login />} />
      <Route exact path="/dasboard/:port/:page" element={<Dasboard />} />
    </Routes>
  );
}

export default App;
