import { React, useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import Check from '../tools/Check';
import 'bootstrap/dist/css/bootstrap.min.css'
import icon from '../logo512.png'
import './Dashboard.css'
import { sizeFormatter } from 'human-readable'
let format = sizeFormatter({
  std: 'JEDEC', // 'SI' (default) | 'IEC' | 'JEDEC'
  decimalPlaces: 2,
  keepTrailingZeroes: false,
  render: (literal, symbol) => `${literal} ${symbol}B`,
})

const Dasboard = () => {
  let { port, page } = useParams();
  const [cpuPer, setCpuPer] = useState('10%');
  const [ramPer, setramPer] = useState('30%');
  const [datas, setDatas] = useState({});
  let pwd = localStorage.getItem('pwd');
  const ync = async () => {
    if (!await Check(localStorage.getItem('port'), pwd)){ window.location.replace('/') };
  };
  useEffect(() => {
    ync()
  }, [])
  String.prototype.toHHMMSS = function () {
    var sec_num = parseInt(this, 10); // don't forget the second param
    var hours   = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);

    if (hours   < 10) {hours   = "0"+hours;}
    if (minutes < 10) {minutes = "0"+minutes;}
    if (seconds < 10) {seconds = "0"+seconds;}
    return hours+':'+minutes+':'+seconds;
}
  setInterval(async () => {
    const raw = await fetch(`${localStorage.getItem('port')}/data/`);
    const data = await raw.json();
    setCpuPer(`${(data.cpuTotal / 10000000).toFixed()}%`)
    setDatas(data);
    setramPer(`${(data.ramUsage / data.ramTTL * 100).toFixed()}%`);
  }, 3000);
  
  if (page == 'home'){
    return (
      <div>
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
          <div class="container-fluid">
            <a class="navbar-brand" href="#">
            <img src={icon} alt="" width="30" height="24" class="d-inline-block align-text-top"/>
              Games Wabot Monitor</a>
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
              <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNav">
              <ul class="navbar-nav">
                <li class="nav-item">
                  <a class="nav-link active" aria-current="page" href="#">Home</a>
                </li>
                <li class="nav-item">
                  <a class="nav-link" href="#" onClick={() => window.location.replace(`/dasboard/${port}/specs`)}>Specification</a>
                </li>
                {/* <li class="nav-item">
                  <a class="nav-link" href="#">Pricing</a>
                </li> */}
              </ul>
            </div>
          </div>
        </nav>
        {/* Start */}
        <center>
          <table>
            <tr>
              <td>
                <div class="progress" style={{transform: "rotate(-90deg)", width: "10em", marginTop: "5cm"}}>
                  <div class="progress-bar" role="progressbar" style={{width: cpuPer}}></div>
                </div>
                <h6>CPU</h6>
                <p>{cpuPer}</p>
              </td>
              <td>
                <div class="progress" style={{transform: "rotate(-90deg)", width: "10em", marginTop: "5cm"}}>
                  <div class="progress-bar" role="progressbar" style={{width: ramPer}}></div>
                </div>
                <h6>RAM</h6>
                <p>{ramPer}</p>
              </td>
              <td>
                <table>
                  <th>
                    <td>Stats</td>
                  </th>
                  <tr>
                    <td>Group Chat<canvas width={20} height={0}></canvas></td>
                    <td>:<canvas width={20} height={0}></canvas></td>
                    <td><b>{datas.groupCh}</b></td>
                  </tr>
                  <tr>
                    <td>Group Join<canvas width={20} height={0}></canvas></td>
                    <td>:<canvas width={20} height={0}></canvas></td>
                    <td><b>{datas.groupJoin}</b></td>
                  </tr>
                  <tr>
                    <td>Group Left<canvas width={20} height={0}></canvas></td>
                    <td>:<canvas width={20} height={0}></canvas></td>
                    <td><b>{datas.groupLeft}</b></td>
                  </tr>
                  <tr>
                    <td>Personal Chat<canvas width={20} height={0}></canvas></td>
                    <td>:<canvas width={20} height={0}></canvas></td>
                    <td><b>{datas.personal}</b></td>
                  </tr>
                  <tr>
                    <td><b>Total Chat</b><canvas width={20} height={0}></canvas></td>
                    <td><b>:</b><canvas width={20} height={0}></canvas></td>
                    <td><b>{datas.chat}</b></td>
                  </tr>
                  <tr>
                    <td><b>Ping</b><canvas width={20} height={0}></canvas></td>
                    <td><b>:</b><canvas width={20} height={0}></canvas></td>
                    <td><b>{datas.speed} MS</b></td>
                  </tr>
                </table>
              </td>
            </tr>
          </table>
          <canvas height={1} width={5000} style={{backgroundColor: "black"}}></canvas>
        </center>
        <center><h3>Status</h3></center>
        <table style={{marginLeft: '2cm'}}>
          <tr>
            <td><b>Ram Total</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{format(datas.ramTTL)}</b></td>
          </tr>
          <tr>
            <td><b>Ram Usage</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{format(datas.ramUsage)}</b></td>
          </tr>
          <tr>
            <td><b>Free Ram</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{format(datas.ramFree)}</b></td>
          </tr>
          <tr>
            <td><b>Node</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{datas.nodeUse}</b></td>
          </tr>
          <tr>
            <td><b>Cpu Model</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{datas.cpuModel}</b></td>
          </tr>
          <tr>
            <td><b>Cpu Speed</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{datas.cpuSpeed} MHZ</b></td>
          </tr>
          <tr>
            <td><b>Cpu Total</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{(datas.cpuTotal / 10000000).toFixed(2)}%</b></td>
          </tr>
          <tr>
            <td><b>Arch</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{datas.arch}</b></td>
          </tr>
          <tr>
            <td><b>Home Dir</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{datas.homeDir}</b></td>
          </tr>
          <tr>
            <td><b>Host Name</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{datas.hostName}</b></td>
          </tr>
          <tr>
            <td><b>Platform</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{datas.platform}</b></td>
          </tr>
          <tr>
            <td><b>Machine</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{datas.machine}</b></td>
          </tr>
          <tr>
            <td><b>System Uptime</b><canvas width={50} height={0}></canvas></td>
            <td><b>:</b><canvas width={50} height={0}></canvas></td>
            <td><b>{`${datas.upTime}`.toHHMMSS()}</b></td>
          </tr>
        </table>
        
      </div>
    )
  }else if(page == 'specs'){
    return (
      <div>
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
          <div class="container-fluid">
            <a class="navbar-brand" href="#">
            <img src={icon} alt="" width="30" height="24" class="d-inline-block align-text-top"/>
              Games Wabot Monitor</a>
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
              <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNav">
              <ul class="navbar-nav">
                <li class="nav-item">
                  <a class="nav-link" aria-current="page" href="#" onClick={() => window.location.replace(`/dasboard/${port}/home`)}>Home</a>
                </li>
                <li class="nav-item">
                  <a class="nav-link active" href="#">Specification</a>
                </li>
                {/* <li class="nav-item">
                  <a class="nav-link" href="#">Pricing</a>
                </li> */}
              </ul>
            </div>
          </div>
        </nav>
        <button className='btn btn-danger' onClick={()=> {
          localStorage.setItem('pwd', '')
          window.location.reload()
      }}>LogOut</button>
      </div>
    )
  }else {
    return (
      <div>
        <center>404 Page Not Found</center>
      </div>
    )
  }
  
}

export default Dasboard