import urlExist from 'url-exist';

const Check = async (port, pwd)=> {
  const ip = `${port}/db/`;
  const chk = await urlExist(ip);
  if (await chk) {
    const raw = await fetch(ip);
    const dt = await raw.json();
    if (dt.db.pw == pwd&&dt.db.type == 'bot') {
      return true;
    }else { return false }
  }else  { return false }
}

export default Check